import React from 'react';
import CustomHead from 'next/head';
import { node, oneOfType, arrayOf } from 'prop-types';
import Navbar from './Navbar';

const Layout = ({ children }) => (
  <div>
    <CustomHead />
    <Navbar />
    <div className="container">
      {children}
    </div>
  </div>
);

Layout.propTypes = {
  children: oneOfType([
    arrayOf(node),
    node,
  ]).isRequired,
};

export default Layout;
